

import SwiperSubPage from "../swiperSubPage/swiperSubPage.jsx";

//obj
import {razvlecheniePage} from "../../asset/obj/razclechenie/razvlecheniePage.js"; //   
//images
import iconWebsite from "../../asset/img/icon/iconWebsite.svg";
import iconVk from "../../asset/img//icon/icons-vk.svg";

//style
import "../subPages.scss";
const objRazvlechenie = razvlecheniePage[0];
export default function SubPagerazvlechenie1(){
    return(
        <div className="subItem__wrapper _container">
            <div className="subItem__images"><img src={objRazvlechenie.img} alt="" /><h3>{objRazvlechenie.name}</h3></div>
            <div className="subItem__actions">
                <a className="button__buy" target="_blank" rel="noreferrer" href="tel:+79093586211"></a>
                <div className="subItem__actions__link"><a target="_blank" rel="noreferrer" href={objRazvlechenie.webLink}><img src={iconWebsite} alt="" /><strong></strong></a><a target="_blank" rel="noreferrer" href={objRazvlechenie.vkLink}><img src={iconVk} alt="" /><strong></strong></a></div>
            </div>
            <div className="subItem__wrapper__container">
            <div className="subItem__info">
                <h2>{objRazvlechenie.name}</h2>
                <h3>{objRazvlechenie.title}</h3>
                <p>{objRazvlechenie.subtitle}</p>
            </div>
            <div className="subItem__swiper">
                <SwiperSubPage props={razvlecheniePage[0]}/>
            </div>
            <div className="subItem__contact">
                <ul className="subItem__contact__info">
                    <li className="subItem__contact__info__place"><strong> </strong><a href="google.com">{objRazvlechenie.place}</a></li>
                    <li className="subItem__contact__info__adress"><strong></strong>{objRazvlechenie.adress}</li>
                    <li className="subItem__contact__info__performance"><strong> </strong>{objRazvlechenie.performance}</li>
                    <li className="subItem__contact__info__age"><strong> </strong>{objRazvlechenie.age}</li>
                    <li className="subItem__contact__info__price"><strong></strong>{objRazvlechenie.price}</li>
                </ul>
                <div className="subItem__contact__schedule">
                    <h4></h4>
                    <div className="subItem__contact__schedule__info">
                        {/* <h5>26  2023  31  2024</h5> */}
                        <h6> 19:00<br />,  13:00<br />,  18:00</h6>
                    </div>
                </div>
                <div className="subItem__contact__dataPubloscher"> {objRazvlechenie.data}</div>
            </div>
            </div>
            
        </div>
    )
}
