

import SwiperSubPage from "../swiperSubPage/swiperSubPage.jsx";

//obj
import {edaPage} from "../../asset/obj/eda/edaPage.js"; //   
//images
import iconWebsite from "../../asset/img/icon/iconWebsite.svg";
import iconVk from "../../asset/img//icon/icons-vk.svg";

//style
import "../subPages.scss";
const objEdaPageItem = edaPage[3];
export default function SubPageEda4(){
    return(
        <div className="subItem__wrapper _container">
            <div className="subItem__images"><img src={objEdaPageItem.img} alt="" /><h3>{objEdaPageItem.name}</h3></div>
            <div className="subItem__actions">
                <a className="button__buy" target="_blank" rel="noreferrer" href="tel:+79277655777"></a>
                <div className="subItem__actions__link"><a target="_blank" rel="noreferrer" href={objEdaPageItem.webLink}><img src={iconWebsite} alt="" /><strong>-</strong></a><a target="_blank" rel="noreferrer" href={objEdaPageItem.vkLink}><img src={iconVk} alt="" /><strong></strong></a></div>
            </div>
            <div className="subItem__wrapper__container">
            <div className="subItem__info">
                <h2>{objEdaPageItem.name}</h2>
                <h3>{objEdaPageItem.title}</h3>
                <p>{objEdaPageItem.subtitle}</p>
            </div>
            <div className="subItem__swiper">
                <SwiperSubPage props={edaPage[3]}/>
            </div>
            <div className="subItem__contact">
                <ul className="subItem__contact__info">
                    <li className="subItem__contact__info__place"><strong> </strong><a href="google.com">{objEdaPageItem.place}</a></li>
                    <li className="subItem__contact__info__adress"><strong></strong>{objEdaPageItem.adress}</li>
                    {/* <li className="subItem__contact__info__performance"><strong> </strong>{objEdaPageItem.performance}</li> */}
                    <li className="subItem__contact__info__age"><strong> </strong>{objEdaPageItem.age}</li>
                    <li className="subItem__contact__info__price"><strong></strong>{objEdaPageItem.price}</li>
                </ul>
                <div className="subItem__contact__schedule">
                    <h4></h4>
                    <div className="subItem__contact__schedule__info">
                        {/* <h5>26  2023  31  2024</h5> */}
                        <h6> - :  10:00  22:00 <br /> - :  10:00  22:30</h6>
                    </div>
                </div>
                <div className="subItem__contact__dataPubloscher"> {objEdaPageItem.data}</div>
            </div>
            </div>
            
        </div>
    )
}
