 import { Link } from "react-router-dom";

// style
import "./header.scss";
// images
import header_logo from "../../asset/img/icon/Group 1.svg";
import header_location from "../../asset/img/header/location.svg";
// import header_search from "../../asset/img/header/search.svg";

export default function Header(){
    return(
        <header className="header"> 
            <section className="header__top">
                <section className="header__top__container _container">
                    <div className="header__top__logo"><Link to={'/'}><img src={header_logo} alt="logo" /></Link></div>
                    <div className="header__top__city"><img src={header_location} alt="city" /></div>
                    {/* <div className="header__top__search"><label htmlFor=""><input name="s" placeholder=" ..." type="search"/><button type="submit"><img src={header_search} alt="" /></button></label></div> */}
                    <div className="header__top__rate">   </div>
                </section>
            </section>
            <section className="header_center _container">
                <nav>
                    <ul>
                        <li><Link to={'kultura'}></Link></li>
                        <li><Link to={'kino'}></Link></li>
                        <li><Link to={'razvlechenie'}></Link></li>
                        <li><Link to={'education'}></Link></li>
                        {/* <li><Link to={'kino'}></Link></li> */}
                        <li><Link to={'vzroslie'}> </Link></li>
                    </ul>
                </nav>
            </section>
            <section className="header_bottom">
                <div className="header_bottom_container _container">
                    <nav>
                        <ul>
                            <li ><Link to={'food'}></Link></li>
                            <li ><Link to={'children'}></Link></li>
                            <li ><Link to={'nature'}> </Link></li>
                            <li ><Link to={'news'}></Link></li>
                            <li><Link to={'zdorovie'}></Link></li>
                        </ul>
                    </nav>
                </div>
            </section>
        </header>
    )
}
