import SwiperSubPage from "../swiperSubPage/swiperSubPage.jsx";

//obj
import {edaPage} from "../../asset/obj/eda/edaPage.js";
//images
// import iconWebsite from "../../asset/img/icon/iconWebsite.svg";
//style
import "../subPages.scss";

export default function SubPageEda2(){
    const edaPageItem = edaPage[1];
    return(
        <>
        <div className="subItem__wrapper _container">
            <div className="subItem__images"><img src={edaPageItem.img} alt="" /><h3>{edaPageItem.name}</h3></div>
            <div className="subItem__actions">
                <a className="button__buy" target="_blank" rel="noreferrer" href={edaPageItem.placeLink}>  </a>
                {/* <div className="subItem__actions__link"><a target="_blank" rel="noreferrer" href="google.com"><img src={iconWebsite} alt="" /><strong>WebSite</strong></a></div> */}
            </div>
            <div className="subItem__wrapper__container">
            <div className="subItem__info">
                <h2>{edaPageItem.name}</h2>
                <h3>{edaPageItem.title}</h3>
                <p>{edaPageItem.subtitle}</p>
            </div>
            <div className="subItem__swiper">
                <SwiperSubPage props={edaPage[1]}/>
            </div>
            <div className="subItem__contact">
                <ul className="subItem__contact__info">
                    <li className="subItem__contact__info__place"><strong> </strong><a target="_blank" rel="noreferrer" href={edaPageItem.placeLink}>{edaPageItem.place}</a></li>
                    <li className="subItem__contact__info__adress"><strong></strong>{edaPageItem.adress}</li>
                    <li className="subItem__contact__info__performance"><strong> </strong>{edaPageItem.performance}</li>
                    <li className="subItem__contact__info__age"><strong> </strong>{edaPageItem.age}</li>
                    <li className="subItem__contact__info__price"><strong></strong>{edaPageItem.price}</li>
                </ul>
                <div className="subItem__contact__schedule">
                    <h4></h4>
                    <div className="subItem__contact__schedule__info">
                        {/* <h5>26  2023  31  2024</h5> */}
                        <h6> 9:00 - 20:00<br /> 10:00 - 19:00</h6>
                    </div>
                </div>
                <div className="subItem__contact__dataPubloscher"> {edaPageItem.data}</div>
            </div>
            </div>
            
        </div>
        </>
    )
}
